body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*Не забывать побить стили*/
html, body, #root {
  min-height: 100vh;
  min-width: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.paper {
  background-color: #fff;
  box-shadow: 0 2px  10px 2px rgba(0, 0, 0, .1);
}
.page {
  color: rgba(0, 0, 0, .7);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, .1);
}

.header {
  z-index: 1;
  position: fixed;
  width: 100vw;
  overflow: hidden;
  font-family: arial;


}

.logo {

  font-style: italic;
  font-size: 20px;
  font-weight: 700;
}

.white {
  text-decoration: none;
  color: white;
}

.medium {
  font-size: 20px;
}

.big {
  font-size: 30px;
  font-weight: 700;
}

.small {
  font-size: 14px;
}

.content {
  margin-top: 60px;
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
}

.center {
  height: 60px;
  margin: 0 auto;
  padding: 0px 20px 0px 20px;
  max-width: 1280px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.footer {
  -webkit-flex: 0 0 auto;
          flex: 0 0 auto;
}
.card {
  padding: 20px 30px;
  margin: 10px;
  border-radius: 25px;
  overflow: hidden;
}
.nav {

  padding-top: 60px;
  width: 300px;
  height: 100%;

}
.menu__background.visible {
  left: 0;
}

.nav__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}



.nav__link:active {
  background-color: rgba(0, 0, 0, .07);
}

.nav__link {
  padding: 20px 0;
  border-bottom: 2px solid rgba(0, 0, 0, .7);
  color: rgba(0, 0, 0, .7);
  text-decoration: none;
  text-align: center;
  display: block;
  font-size: 30px;
}
.hamburger {
  cursor: pointer;
}

.footer__center {
  margin: 0 auto;
}

.hamburger:active {
  background-color: rgba(0, 0, 0, .07);
}
.mail {
  color: rgba(0, 0, 0, .7);
  padding-left: 5px;

}
.wrapper {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.menu__background {
  position: fixed;
  left: -100vw;
  width: 100vw;
  transition: all 400ms;
  height: 100vh;
}

.image {
  float: right;
  margin-left: 5px;
  margin-bottom: 5px;
  border-radius: 10px;

}

.no-style {
  text-decoration: none;
  color: inherit;
}

@media (min-width: 1280px) {
  .content {
    -webkit-flex: 1 0 auto;
            flex: 1 0 auto;
    margin: 60px auto 0 auto;
    width: 1280px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-flex-direction: row;
            flex-direction: row;
  }
  .card {
    width: 550px;

  }

  .nav__link:hover {
    background-color: rgba(0, 0, 0, .04);
  }
  .hamburger:hover {
    background-color: rgba(0, 0, 0, .04);
  }

}

@media (max-width: 410px) {
  .big, .medium {
    font-size: 1em;
  }
  .small {
    font-size: 0.8em;
  }
}

@media (max-width: 620px) {
  .image {
    width: 80%;
    float: none;
    display: block;

    margin: 10px auto;
  }
}

